.MarkdownRender {
  word-wrap: break-word; }
  .MarkdownRender blockquote {
    border-left: 4px solid #228be6;
    padding: 1rem;
    background: #f1f3f5;
    margin-left: 0;
    margin-right: 0; }
    .MarkdownRender blockquote p {
      margin: 0; }
  .MarkdownRender h1, .MarkdownRender h2, .MarkdownRender h3, .MarkdownRender h4 {
    font-weight: 500; }
  .MarkdownRender h1 code, .MarkdownRender h2 code, .MarkdownRender h3 code, .MarkdownRender h4 code, .MarkdownRender h5 code, .MarkdownRender p code {
    font-family: 'D2 Coding';
    background: #f8f9fa;
    padding: 0.25rem;
    color: #228be6;
    border: 1px solid #e9ecef;
    border-radius: 2px; }
  .MarkdownRender code[class*="language-"], .MarkdownRender pre[class*="language-"] {
    font-family: 'D2 Coding'; }
  .MarkdownRender a {
    color: #228be6; }
    .MarkdownRender a:hover {
      color: #339af0;
      text-decoration: underline; }
  .MarkdownRender table {
    border-collapse: collapse;
    width: 100%; }
  .MarkdownRender table, .MarkdownRender th, .MarkdownRender td {
    border: 1px solid #ced4da; }
  .MarkdownRender th, .MarkdownRender td {
    font-size: 0.9rem;
    padding: 0.25rem;
    text-align: left; }
  .MarkdownRender img {
    max-width: 100%;
    margin: 0 auto;
    display: block; }
  .MarkdownRender iframe {
    margin: 0 auto;
    max-width: 100%;
    display: block; }



