.DisqusFrame {
  margin: 0 auto;
  width: 912px;
  padding-left: 16rem;
  padding-right: 1rem; }
  @media (max-width: 1023px) {
    .DisqusFrame {
      width: 768px; } }
  @media (max-width: 767px) {
    .DisqusFrame {
      width: calc(100% - 2rem);
      padding-left: 1rem; } }



